let initTemplate =(props) =>{

	let _this = this;
	props.createUIDom(
		{
			pagecode: pageId,//页面id
			appid: appid//注册按钮的id
		},
		function (data) {
			if (data) {
				if (data.template) {
					let meta = data.template;
					meta = modifierMeta(props, meta)
                    props.meta.setMeta(meta);
                    data.button && props.button.setButtons(data.button);
                    ajax({
                        url: queryListUrl,
                        data:{
                            pagecode: pageId
                        },
                        success: (res) => {
                            if(res.data){
                                props.table.setAllTableData(tableId, res.data[tableId]);
                            }else{
                                toast({content:"无数据",color:"warning"});
                            }
                        },
                        error : (res)=>{
                            console.log(res.message);
                        }
					});
					props.button.setButtonsVisible({
						Add: true,
						Edit: true,
						//Delete: true,
						Copy:true,
						Save: false,
						Cancel: false,
						SetToDefault:true,
						Refresh: true,
						Print: true
					});
                    
                }
				// if (data.button) {
				// 	let button = data.button;
				// 	props.button.setButtons(button);
				// 	//props.button.setPopContent('delline','确认要删除该信息吗？') /* 设置操作列上删除按钮的弹窗提示 */
				// }
			}
		}
	)
}

