function tableButtonClick(props, id, text, record, index){
	switch(id){

		case 'EditLine':
			props.linkTo('../card/index.html',{
				status:'edit',
				id: record[pk_item].value
			})
			break;
		case 'Copy':
			props.linkTo('../card/index.html',{
				status:'add',
				id: record[pk_item].value
			})
			break;
		case "SetToDefault":
			let data = {
				pageid:pageId,
				id: record[pk_item].value,
				model : {
					areaType: "table",
					pageinfo: null,
					rows: []
				}
			};
			//data.model.rows = tableData;
			ajax({
				url: setDefaultUrl,
				data:data,
				success: (res) => {
					toast({color:"success",content:"成功"});
					//this.refreshAction(props);
				}
			});
			break;
		case 'DelLine':
			ajax({
				url: deleteUrl,
				data: 
				{
					pk_formatdoc: record[pk_item].value
				},
				success: (res) => {
					if (res.success) {
						toast({ color: 'success', content: '删除成功' });
						props.table.deleteTableRowsByIndex(tableId, index);
					}
				}
			});
			break;
		default:
            console.log(id, index);
            break;

	}
}

